import { toTimestamp, fromTimestampToString } from '../helper.js';

class SaleStore{

  constructor(){
    riot.observable(this);
    this.productItems = [];

    this.db = riot.db;
    this.pageSize = riot.pageSize;
    this.offset = riot.offset;
    this.bindEvents();
  }

  bindEvents(){
    var self = this;
    let salesTriggerChangeds = (d) => {
      // console.log(d)
      this.trigger(riot.EVT.salesLoadItemsSuccess, d);  
    };

    let salesTotalTriggerChangeds = (d) => {
      
      var grandTotal = _.sumBy(d.docs, function(o) { return o.total; })
      // console.log(grandTotal)
      this.trigger(riot.EVT.salesTotalSuccess, grandTotal);  
    };

    let salesSave = (item) => {
      // console.log(item);
      this.db.put(item, function callback(err, result) {
        if (!err) {
          this.trigger(riot.EVT.salesSaveSuccess, item._id);
        } else {
          console.log(err);
        }
      }.bind(this)); 
    };

    let salesGet = (id) => {
      this.db.allDocs({
        startkey: id, 
        endkey: id+'\uffff', 
        include_docs: true,
      }).then(function (result) {
        // handle result
        // productsTriggerChangeds(result.rows.map(function(r){ 
          // console.log(result.rows[0].doc);
          self.trigger(riot.EVT.salesGetSuccess, result.rows[0].doc);
        // return r.doc;
        // }));
      }).catch(function (err) {
        console.log(err);
      });
    }

    let salesLoadItems = (date) => {
      /* FIND METHOD */
      var startdate = toTimestamp(fromTimestampToString(date) + " 00:00:00");
      var enddate = toTimestamp(fromTimestampToString(date) + " 23:59:00");
      self.db.createIndex({
           index: {
            fields: ['created', 'type'],
            name: 'mysalesindex',
            ddoc: 'mysalestdoc',
            type: 'json',
          }
      }).then(function() {
          return self.db.find({
              selector: {'created': {$gt:startdate, $lt:enddate}, 'type': 'sales'},
              fields: ['created', 'discount','type','payment', '_id', 'total', 'cash', 'items'],
              sort: [{'created': 'desc'}],
              limit: self.pageSize, skip: self.offset, include_docs : true
          }).then(function (result) {
            // console.log(result);
            // return result.docs;
            salesTriggerChangeds(result);
            // self.trigger(riot.EVT.searchItemSuccess, result);
            // return Promise.resolve(result);
          }).catch(function (err) {
            console.log(err);
          });
      }).then(function () {
        return self.db.getIndexes().then(function (result) {
          result.indexes.forEach(function (value, i) {
            if(value.name == "mysalesindex") {
              self.db.deleteIndex({
                "name": "mysalesindex",
                "ddoc": value.ddoc,
              })  
            }
          });
        }).catch(function (err) {
          // ouch, an error
        });
      });

    }

    let salesTotal = (date) => {
      
      /* FIND METHOD */
      var startdate = toTimestamp(fromTimestampToString(date) + " 00:00:00");
      var enddate = toTimestamp(fromTimestampToString(date) + " 23:59:00");
      self.db.createIndex({
           index: {
            fields: ['created', 'type'],
            name: 'mysalestotalindex',
            ddoc: 'mysalestotaldoc',
            type: 'json',
          }
      }).then(function() {
          return self.db.find({
              selector: {'created': {$gte:startdate, $lte:enddate}, 'type': 'sales'},
              fields: ['created', 'type','total'],
              sort: [{'created': 'desc'}],
              include_docs : true
          }).then(function (result) {
            // console.log(result);
            // return result.docs;
            salesTotalTriggerChangeds(result);
            // self.trigger(riot.EVT.searchItemSuccess, result);
            // return Promise.resolve(result);
          }).catch(function (err) {
            console.log(err);
          });
      }).then(function () {
        return self.db.getIndexes().then(function (result) {
          result.indexes.forEach(function (value, i) {
            if(value.name == "mysalestotalindex") {
              self.db.deleteIndex({
                "name": "mysalestotalindex",
                "ddoc": value.ddoc,
              })  
            }
          });
        }).catch(function (err) {
          // ouch, an error
        });
      });
    }

    let salesSearchItem = (text) => {
      var s = text.text;
      var regexp = new RegExp(s, 'i'); // Case Insensitive
      this.db.find({
        selector: {
            type: "sales",
            title: {$regex: regexp}
          }
      }).then(function (result) {
        console.log(result.docs);
        self.trigger(riot.EVT.salesSearchItemSuccess, result.docs);
      }).catch(function (err) {
        console.log(err);
      });
    }

    this.on(riot.EVT.salesSave, (opts) => {
      salesSave(opts);
    });

    this.on(riot.EVT.salesLoadItems, (opts) => {
      if(opts.page) {
        this.offset = ((parseFloat(opts.page) - 1) * this.pageSize);
      }

      // var date = Math.floor(Date.now() / 1000)
      // if(typeof (opts.date) > 0) date = opts.date;
      salesLoadItems(opts.date);
    });


    this.on(riot.EVT.salesSearchItem, (s) => {
      salesSearchItem(s);
    });

    this.on(riot.EVT.salesGet, (id) => {
      salesGet(id);
    });

    this.on(riot.EVT.salesTotal, (date) => {
      salesTotal(date)
    });
  }

}

// add store to riot control
let saleStore = new SaleStore();
riot.control.addStore(saleStore);

export default saleStore;


