exports.addCommas = function(nStr) {
    nStr += '';
    var x = nStr.split('.');
    var x1 = x[0];
    var x2 = x.length > 1 ? '.' + x[1] : '';
    var rgx = /(\d+)(\d{3})/;
    while (rgx.test(x1)) {
        x1 = x1.replace(rgx, '$1' + '.' + '$2');
    }
    return x1 + x2;
}

exports.avoidDup = function(nObj) {
    var noDup =  
        _.map(
            _.uniq(
                _.map(nObj, function(obj){
                    return JSON.stringify(obj);
                })
            ), function(obj) {
              return JSON.parse(obj);
            }
        )
    return noDup;
}

exports.formattedTime = function(timestamp, dateOnly=false, timezone=7) {
	
	var date = new Date((timestamp - (timezone*60*60))*1000);
    // Day date part from the timestamp
    var day = "0" + date.getDate();
    // Month part from the timestamp
    var month = "0" + (date.getMonth() + 1);
    // Year part from the timestamp
    var year = date.getFullYear();
    // Hours part from the timestamp
    var hours = date.getHours();
    // Minutes part from the timestamp
    var minutes = "0" + date.getMinutes();
    // Seconds part from the timestamp
    var seconds = "0" + date.getSeconds();

    // Will display time in 10:30:23 format
    if(dateOnly)
    	return day.substr(-2) + "/" + month.substr(-2) + "/" + year;
   	else 
   		return day.substr(-2) + "/" + month.substr(-2) + "/" + year + " " + hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);

}

exports.fromTimestampToString = function(ts, separator="/") {
	
	var date = new Date(ts*1000);
	// console.log(date);
    // Day date part from the timestamp
    var day = "0" + (date.getDate());
    // Month part from the timestamp
    var month = "0" + (date.getMonth()+1);
    // Year part from the timestamp
    var year = date.getFullYear();

    // Will display time in 10:30:23 format
    // return year + separator + month.substr(-2) + separator + day.substr(-2)
    return month.substr(-2) + separator + day.substr(-2) + separator + year
}

exports.toTimestamp = function(s) {
	// return Date.parse(s+"-0500")/1000;
    return Date.parse(s+"-0500")/1000;
}


