// import './css/index.css';

import './event-helper';
import './stores/shopstore';
import './stores/cartstore';
import './stores/productstore';
import './stores/salestore';
import './router.js';

import pikaday from 'pikaday';

/* GET SHOP NAME */
var mrShop = {
	map: function (doc) {
	  if (doc.type=="shop") {
	    emit(doc.value, null);
	  }
	}
};
riot.db.query(mrShop, {include_docs : false}).then(function (result) {
  if(result.total_rows > 0) {
    result.rows.map(function(r){ 
      if(r.id === "shop_name") {
        riot.shopname = r.key
        document.getElementById('home').innerHTML = riot.shopname;
      }
      else if(r.id === "shop_timezone") 
      {
        riot.timezone = r.key
      }
    })
    riot.isNewUser = false;
  } else {
    riot.isNewUser = true;
  }
}).catch(function (err) {
	console.log(err);
});

// var Mousetrap = require('mousetrap');

/* SHORTCUT */
Mousetrap.bindGlobal('ctrl+1', function() { 
  document.getElementById('home').click()
  // document.querySelector('[name="acinput"]').focus();
});
Mousetrap.bindGlobal('ctrl+p', function() { riot.route("products"); });
Mousetrap.bindGlobal('ctrl+r', function() { riot.route("sales"); });

Mousetrap.bindGlobal('ctrl+s', function() { document.querySelector('[name="acinput"]').focus() });
Mousetrap.bindGlobal('ctrl+t', function() { 
  document.querySelector('[name="cash"]').focus();
  document.querySelector('[name="cash"]').select();
});
Mousetrap.bindGlobal('ctrl+m', function() { 
  // document.querySelector('[name="cash"]').focus() 
  var parentElement = document.getElementById("myDropdown");
  // console.log(parentElement.childNodes[1].childNodes[2]);
  // var uhi = parentElement.childNodes[1].childNodes[2];
  // uhi.focus();
  document.getElementsByClassName("dropbtn")[0].focus();
  document.getElementById("myDropdown").classList.toggle("show");
});


