class ShopStore{

  constructor(){
    riot.observable(this);
    this.productItems = [];

    this.db = riot.db;
    this.edit_id = false;
    this.pageSize = riot.pageSize;
    this.offset = riot.offset;
    this.bindEvents();
  }

  bindEvents(){
    var self = this;
    

    let shopLoadData = () => {
      /* ALLDOCS METHOD */
      // this.db.allDocs({
      //     // descending: true,
      //     startkey: 'cartitem_', 
      //     endkey: 'cartitem_\uffff', 
      //     include_docs: true}, function(err, doc) {
      //       console.log(doc);
      //   cartTriggerChangeds(doc.rows.map(function(r){ return r.doc;}));
      // });

      /* QUERY METHOD */
      var myMapReduceFun2 = {
        map: function (doc) {
          if (doc.type=="shop") {
            // emit([doc.name.toLowerCase(), doc.cat.toLowerCase()])
            emit(doc.value, null);
          }
        }
      };
      this.db.query(myMapReduceFun2, {include_docs : false}).then(function (result) {

        var uu = []

        uu = result.rows.map(function(r){ 
          if(r.id === "shop_name") return {'name': r.key}
          if(r.id === "shop_timezone") return {'timezone': r.key}
        })

        var items = [];
        for (var key in result.rows) {
          var type = result.rows[key].id
          var value = result.rows[key].key
          if(type === "shop_name") items['name'] = value;
          if(type === "shop_timezone") items['timezone'] = value;
        }
        self.trigger(riot.EVT.shopLoadDataSuccess, items);  
        // return (result.rows[0].key)
        
      }).catch(function (err) {
        console.log(err);
      });

      
    }

    let shopSaveData = (item) => {
      
      this.db.put(item, function callback(err, result) {
        if (!err) {
          self.trigger(riot.EVT.shopSaveSuccess, item.value);
        } else {
          if(err.status == 409) {
            shopUpdateData(item, {value:item.value})
          } else {
            console.log(err);
          }
        }
      }); 
    };

    let shopUpdateData = (item, params) => {
      // console.log(item);
      this.db.get(item._id).then(function (origDoc) {
        item._rev = origDoc._rev;
        item.value = params.value;
        self.trigger(riot.EVT.shopUpdateSuccess, item.value);
        return self.db.put(item);
      }).catch(function (err) {
        if (err.status === 409) {
          console.log(err);
        } else { // new doc
          return self.db.put(item);
        }
      });
    };

    this.on(riot.EVT.shopSaveName, (s) => {
      var updatedOn = Math.floor((Date.now() + (riot.timezone*60*60*1000)) / 1000)
      var item = {
        _id: 'shop_name',
        value: s,
        type: 'shop',
        updated: updatedOn
      };
      shopSaveData(item);
    });

    this.on(riot.EVT.shopSaveTimezone, (s) => {
      var updatedOn = Math.floor((Date.now() + (riot.timezone*60*60*1000)) / 1000)
      var item = {
        _id: 'shop_timezone',
        value: s,
        type: 'shop',
        updated: updatedOn
      };
      shopSaveData(item);
    });


    this.on(riot.EVT.shopLoadData, () => {
      shopLoadData();
    });
  }

}

// add store to riot control
let shopStore = new ShopStore();
riot.control.addStore(shopStore);

export default shopStore;


