class CartStore{

  constructor(){
    riot.observable(this);
    this.cartItems = [];

    this.db = riot.db;
    // this.db = new PouchDB('todos');
    // this.remoteCouch = true;

    this.bindEvents();
  }

  bindEvents(){
    var self = this;
    let cartTriggerChangeds = (d) => {
      this.cartItems = d;
      this.trigger(riot.EVT.cartLoadItemsSuccess, this.cartItems);  
      // console.log(d);
    };

    let clearDB = () => {
      this.db.destroy('todos').then(function() { 
        self.db = new PouchDB('todos');
      });
      this.trigger(riot.EVT.clearDbSuccess, []);  
    }

    let cancelCart = () => {
      self.db.allDocs({startkey: 'cartitem_', endkey: 'cartitem_\uffff'}).then(function (result) {
        // Promise isn't supported by all browsers; you may want to use bluebird
        return Promise.all(result.rows.map(function (row) {
          return self.db.remove(row.id, row.value.rev);
        }));
        
      }).then(function () {
        // this.trigger(riot.EVT.cancelCartSuccess, []);  
        // done!
      }).catch(function (err) {
        // error!
      });

      // this.db.allDocs({
      //     startkey: 'cartitem_', 
      //     endkey: 'cartitem_\uffff',
      //   }, 
      //   function(err, doc) {
      //     // console.log(doc.rows);
      //     self.db.get(doc).then(function(doc) {
      //       return db.remove(doc);
      //     }).then(function (result) {
      //       // handle result
      //     }).catch(function (err) {
      //       console.log(err);
      //     });
      // });
    }

    let cartLoadItems = () => {
      /* ALLDOCS METHOD */
      // this.db.allDocs({
      //     // descending: true,
      //     startkey: 'cartitem_', 
      //     endkey: 'cartitem_\uffff', 
      //     include_docs: true}, function(err, doc) {
      //       console.log(doc);
      //   cartTriggerChangeds(doc.rows.map(function(r){ return r.doc;}));
      // });

      /* QUERY METHOD */
      // var myMapReduceFun2 = {
      //   map: function (doc) {
      //     if (doc.type=="cartitem") {
      //       // emit([doc.name.toLowerCase(), doc.cat.toLowerCase()])
      //       emit(doc.name, null);
      //     }
      //   }
      // };
      // this.db.query(myMapReduceFun2, {include_docs : true, descending: true}).then(function (result) {
      //   cartTriggerChangeds(result.rows.map(function(r){ return r.doc;}));
      //   // console.log(result)
        
      // }).catch(function (err) {
      //   console.log(err);
      // });

      /* FIND METHOD */
      self.db.createIndex({
           index: {
            fields: ['created', 'type'],
            name: 'mycartindex',
            ddoc: 'mydesigndoc',
            type: 'json',
          }
      }).then(function() {
          return self.db.find({
              selector: {'created': {$gt: 0}, 'type': 'cartitem'},
              fields: ['created', 'price','type','title','qty', '_id'],
              sort: [{'created': 'desc'}],
          }).then(function (result) {
            // console.log(result);
            // return result.docs;
            cartTriggerChangeds(result.docs.map(function(r){ return r;}));
            // self.trigger(riot.EVT.searchItemSuccess, result);
            // return Promise.resolve(result);
          }).catch(function (err) {
            console.log(err);
          });
      }).then(function () {
        return self.db.getIndexes().then(function (result) {
          result.indexes.forEach(function (value, i) {
            if(value.name == "mycartindex") {
              self.db.deleteIndex({
                "name": "mycartindex",
                "ddoc": value.ddoc,
              })  
            }
          });
        }).catch(function (err) {
          // ouch, an error
        });
      });
    }

    let cartAddTodo = (item) => {
      // console.log(item);
      this.db.put(item, function callback(err, result) {
        if (!err) {
          // console.log('ADD')
          self.trigger(riot.EVT.cartSaveItemSuccess, item);
        } else {
          if(err.status == 409) {
            cartUpdateTodo(item, {mode:'inc', qty: 1})
          } else {
            console.log(err);
          }
        }
      }); 
    };

    let cartUpdateTodo = (item, params) => {
      this.db.get(item._id).then(function (origDoc) {
        item._rev = origDoc._rev;
        
        if(params.qty) {
          if(params.mode =="inc")
            item.qty = parseInt(origDoc.qty)+parseInt(params.qty);
          else
            item.qty = parseInt(params.qty);
        }
        _.remove(item, { '_rev': item._rev });
        // console.log('UPDATE')
        // console.log(item)
        self.trigger(riot.EVT.cartUpdateItemSuccess, item);

        return self.db.put(item);
      }).catch(function (err) {
        if (err.status === 409) {
          console.log(err);
        } else { // new doc
          // return self.db.put(item);
        }
      });
    };

    let cartRemoveTodo = (item) => {
      this.db.get(item._id).then(function (origDoc) {
        item._rev = origDoc._rev;
        
        self.db.remove(item);
        return self.trigger(riot.EVT.cartRemoveItemSuccess, item);

      }).catch(function (err) {
        if (err.status === 409) {
          console.log(err)
          // location.reload()
        } else { // new doc
          return self.db.remove(item);
        }
      });
    };

    let cartSearchItem = (text) => {
      var s = text.text;
      // console.log(s.text);
      /* MAP QUERY METHOD */
      // var myMapReduceFun = {
      //   map: function (doc) {
      //     if (doc.type=="item") {
      //       emit(doc.title, null);
      //     }
      //   }
      // };
      // this.db.query(myMapReduceFun, {startkey: s, endkey: s+'\uffff', limit: 10}).then(function (result) {
      //   self.trigger(riot.EVT.cartSearchItemSuccess, result.rows);
      //   console.log("Query");
      //   console.log(result.rows);
      //   // console.log(s);
      //   return Promise.resolve(result);
      // }).catch(function (err) {
      //   console.log(err);
      // });

      /* FIND METHOD */
      /* CATATAN PENTING: 
      Perlu dikembangkan untuk membuat INDEX terlebih dahulu */
      
      var regexp = new RegExp(s, 'i'); // Case Insensitive
      this.db.find({
        selector: {
            type: "item",
            title: {$regex: regexp}
          }
      }).then(function (result) {
        // console.log(result);
        self.trigger(riot.EVT.cartSearchItemSuccess, result.docs);
      }).catch(function (err) {
        console.log(err);
      });

      /* FIND METHOD */
      // this.db.createIndex({
      //   index: {
      //       fields: ['title'],
      //       limit:2,
      //       name: 'myindex',
      //     }
      // }).then(function () {
        
      //   return self.db.find({
      //     selector: {title: {$regex: s.text}},
      //     limit:5
      //   }).then(function (result) {
      //     // console.log(result);
      //     self.trigger(riot.EVT.searchItemSuccess, result);
      //     return Promise.resolve(result);
      //   }).catch(function (err) {
      //     console.log(err);
      //   });
      // }).then(function () {
      //   return self.db.getIndexes().then(function (result) {
      //     result.indexes.forEach(function (value, i) {
      //       if(value.name == "myindex") {
      //         self.db.deleteIndex({
      //           "name": "myindex",
      //           "ddoc": value.ddoc,
      //         })  
      //       }
      //     });
      //   }).catch(function (err) {
      //     // ouch, an error
      //   });
      // });
    }

    this.on(riot.EVT.cartLoadItems, () => {
      cartLoadItems();
    });

    this.on(riot.EVT.cartRemoveItem, (opts) => {
      cartRemoveTodo(opts);
    });

    this.on(riot.EVT.cartSaveItem, (opts) => {
      
      var _myId = opts.title;
      _myId = _myId.replace(/\W/g,'');

      var createdOn = Math.floor(Date.now() / 1000)

      var item = {
        _id: 'cartitem_'+_myId,
        title: opts.title,
        qty: 1,
        type: 'cartitem',
        created: createdOn
      };

      this.db.get('item_'+_myId).then(function (origDoc) {
        item.price = origDoc.price;
        cartAddTodo(item);
      }).catch(function (err) {
        if (err.status === 409) {
          console.log(err);
        } else { // new doc
          console.log(err)
        }
      });



      
    });

    this.on(riot.EVT.clearDb, () => {
      clearDB();
    });

    this.on(riot.EVT.cartSearchItem, (s) => {
      cartSearchItem(s);
    });

    this.on(riot.EVT.cancelCart, (mode) => {
      cancelCart();
      if(mode.print) {
        // Mode = Print;
        // console.log("print");
        // this.trigger(riot.EVT.cancelCartPrintSuccess, []);  
      } else {
        this.trigger(riot.EVT.cancelCartSuccess, []);  
      }
      
    });

    this.on(riot.EVT.cartEditItemOn, (s) => {
      cartUpdateTodo(s, {qty: s.qty})
    });
  }

}

// add store to riot control
let cartStore = new CartStore();
riot.control.addStore(cartStore);

export default cartStore;


